* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  padding-top: 0 50px;
  justify-content: space-between;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.logo {
  justify-content: center;
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.nav {
  align-content: space-between;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  justify-content: center;
  margin-left: 20px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-align: center;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  list-style: none;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.is-active {
  color: #00acdc;
  display: block;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  padding-inline: 3px;
  display: flex;
}

.card__description {
  color: #060b35;
  flex-direction: column;
  justify-content: center;
  height: 36px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.card__code {
  color: #616070;
  flex-direction: column;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__stars-stars {
  gap: 4px;
  display: flex;
}

.card__stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__stars-review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
  position: relative;
  top: 1px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__description {
  color: #34568b;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
